* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --bg-color: #e0e0e0;
    --text-color: #1b1b1b;
    --accent-color: rgb(8, 33, 57);
    --header-color: #ffffff;
    --active-color: orange;
}

body {
    font-family: Arial;
    background: var(--bg-color);
}

header {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    background: var(--accent-color);
    height: 80px;
}

header a {
    text-decoration: none;
    font-size: 18px;
    color: var(--header-color);
}

.container {
    max-width: 900px;
    margin: 0 auto;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.container a {
    color: var(--text-color);
    text-decoration: none;
}

.container a:hover {
    text-decoration: underline;
}

footer {
    height: 80px;
    text-align: center;
    background: var(--text-color);
    font-size: 18px;
    color: var(--header-color);
    display: flex;
    align-items: center;
    justify-content: center;
}